import React from 'react';
import '../css/CircularContainer.css'; // 

const CircularContainer = ({ percentageSaved }) => {
    const isGoalAchieved = percentageSaved === 100; // 
    const displayText = percentageSaved > 100 ? "You achieve today's goal!" : `${percentageSaved.toFixed(1)}%`; // 


    return (
        <div className="circular-container">
            <div
                className="circular-level"
                style={{ transform: `scaleY(${Math.min(percentageSaved, 100) / 100})` }} //  100%
            ></div>

            <p className="percentage-text">{displayText}</p> {/*  */}
        </div>
    );
};

export default CircularContainer;
