import "../css/StepCounter.css"; //  CSS 

import React, { useState } from "react";
import CircularContainer from "./CircularContainer"; //  CircularContainer 

const StepCounter = ({ onStepChange }) => {
	const [steps, setSteps] = useState(""); // 
	const [distance, setDistance] = useState(""); // 
	const [carbonSaved, setCarbonSaved] = useState(0); // 

	// 
	const handleChange = (e) => {
		setSteps(e.target.value); // 
	};

	// 
	const handleSubmit = (e) => {
		e.preventDefault(); // 
		onStepChange(steps); // 
		//  = 0.8 
		const walkingDistance = parseFloat(steps) * 0.8; // 
		setDistance(walkingDistance.toFixed(2)); // 
		//  1  = 0.27  CO2 
		const carbonSavedValue = (walkingDistance / 1000) * 0.27; //  CO2 
		setCarbonSaved(carbonSavedValue.toFixed(2)); //  CO2 
	};

	// 
	const percentageSaved = (carbonSaved / 1) * 100; //  1  CO2  100%

	return (
		<div className="step-counter-container">
			<form onSubmit={handleSubmit}>
				<label>How many steps did you take today?</label>
				<input
					type="number"
					value={steps}
					onChange={handleChange}
					placeholder="Enter the steps"
					required
				/>
				<button type="submit" className="btn btn-outline-success">
					Submit
				</button>
			</form>

			<div className="percentage-saved">
				Percentage of 1Kg of CO2 you saved
			</div>
			{/*  CircularContainer */}
			<CircularContainer percentageSaved={percentageSaved} />
			<div></div>
		</div>
	);
};

export default StepCounter;
